@media screen and (min-width: 768px) {
  .description {
    &__title {
      font-size: 3.5rem;
    }
    &__text {
      font-size: 1.6rem;  
    }
    &__button {
      font-size: 1.3rem;
      width: 170px;
      height: 35px;
    }
  }
  .card {
    max-width: 423px;
  }

  .mainContainer {
    display: flex;
    gap: 15px;
    margin: 0 17px;
    margin-top: 21px;
  }
  .preview {
    width: 50%;
    &__imageContainer {
      height: 74.5%;
      width: auto;
    }
  }
  .form {
    width: 50%;
    margin-top: 0;
    &__title {
      font-size: 3rem;
    }
    &__ask-info {
      font-size: 1.6rem;
    }
    &__project {
      &--input {
        font-size: 1.2rem;
      }
      &--textarea {
        font-size: 1.2rem;
      }
    }
    &__autor {
      &--input {
        font-size: 1.2rem;
      }
    }
    &__btn {
      &--item {
        font-size: 1rem;
        &.btn-large {
          width: 184px;
          font-size: 1.6rem;
        }
      }
    }
    &__card {
      font-size: 1.6rem; 
    }
  }

  .card {
    height: 155px;
    width: 300px;
    padding: 25px 20px;
    &__autor {
      &--image {
        width: 80px;
      }
      &--job {
        font-size: 1rem;
      }
      &--name {
        font-size: 1.2rem;
      }
    }
    &__infoProject {
      &--title {
        font-size: 1.5rem;
      }
      &--slogan {
        font-size: 1.2rem;
      }
      &--desc {
        font-size: 0.8rem;
      }
    }
    &__technologies {
      font-size: 1rem;
      &--icons {
        font-size: 1.4rem;
      }
    }
  }
}

@media screen and (min-width: 1024px) {
  .description {
    &__title {
      font-size: 4rem;
    }
    &__text {
      font-size: 1.8rem;
    }
    &__button {
      font-size: 1.5rem;
      width: 250px;
      height: 40px;
    }
  }
  .mainContainer {
    display: flex;
    gap: 25px;
    margin: 0 17px;
    margin-top: 21px;
  }
  .preview {
    width: 50%;
    &__imageContainer {
      height: 70.7%;
    }
  }
  .form {
    width: 50%;
    margin-top: 0;
    &__title {
      font-size: 4rem;
    }
    &__ask-info {
      font-size: 1.8rem;
    }
    &__project {
      &--input {
        font-size: 1.4rem;
      }
      &--textarea {
        font-size: 1.4rem;
      }
    }
    &__autor {
      &--input {
        font-size: 1.4rem;
      }
    }
    &__btn {
      &--item {
        font-size: 1.2rem;
        width: 160px;
        height: 43px;
        &.btn-large {
          width: 184px;
          font-size: 1.6rem;
        }
      }
    }
    &__card {
      font-size: 1.8rem;
    }
  }
  .card {
    height: 200px;
    width: 375px;
    &__autor {
      &--image {
        width: 100px;
      }
      &--job {
        font-size: 1.4rem;
      }
      &--name {
        font-size: 1.8rem;
      }
    }
    &__infoProject {
      &--subtitle {
        font-size: 1rem;
      }
      &--title {
        font-size: 2rem;
      }
      &--slogan {
        font-size: 1.4rem;
      }
      &--desc {
        font-size: 1rem;
      }
    }
    &__technologies {
      font-size: 1.2rem;
      &--icons {
        font-size: 1.6rem;
      }
    }
  }
}
